"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";

export default function LoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);

  async function onSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    try {
      const res = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });
      const data = await res.json();
      if (res.ok) {
        router.push("/dashboard");
      } else {
        alert(data.error || "Login failed");
      }
    } finally {
      setLoading(false);
    }
  }

  return (
    <form onSubmit={onSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium">Email</label>
        <input value={email} onChange={(e) => setEmail(e.target.value)} type="email" required className="mt-1 input" />
      </div>
      <div>
        <label className="block text-sm font-medium">Password</label>
        <input value={password} onChange={(e) => setPassword(e.target.value)} type="password" required className="mt-1 input" />
      </div>
      <button className="w-full btn" disabled={loading}>
        {loading ? "Signing in..." : "Sign in"}
      </button>

      <div className="pt-2 text-center">
        <p className="text-sm">or</p>
        {/* NextAuth sign-in link (client) */}
        <a href="/api/auth/signin/google" className="mt-2 inline-block btn-outline">
          Sign in with Google
        </a>
      </div>

      <div className="pt-4 text-center text-sm">
        <a href="/auth/signup" className="text-blue-600">Create an account</a>
      </div>
    </form>
  );
}
